@import './mixin.sass';
@import './button.sass';

body {
  margin: 0;
  font-family: "Roboto", "Segoe UI", "Fira Sans", "Helvetica Neue", sans-serif;
  background: #f8f9fa;
  touch-action: manipulation;
  background: #334150; }
.dialog {
  font-size: 18px;
  margin: 5px 0 5px;
  color: #fff;
  &__before,
  &__after {
    display: inline; }
  // &--0
  //   font-style: italic
 }  //   color: #ffc312
.form {
  display: flex;
  flex-direction: column;
  padding-top: 1rem; }
.box__items {
  border: 2px dashed #65ddd9;
  padding: 5px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  > div {
    padding: 0 5px;
    border-left: 2px solid #65ddd9;
    border-right: 2px solid #65ddd9;
    margin-left: -2px;
    // &:first-child
    //   border-left: 0
    // &:last-child
 } }    //   border-right: 0
.box {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 23px;
  text-align: center; }
.wrapper {
  padding: 25px 0;
  .MuiSelect-icon {
    color: #ffc312 !important; }
  .MuiSelect-select.MuiSelect-select {
    color: #ffc312 !important;
    text-align: center !important; } }
// ___ styles for input task ___
.MuiFormControl-root {
  margin: 0 5px !important; }
.inputForm {
  .MuiFormControl-root {
    flex-direction: row !important;
    vertical-align: bottom !important; }
  .MuiInputBase-input {
    height: 1rem !important; } }
// ___ styles for select task ___
.MuiSelect-selectMenu {
  min-width: 120px !important; }
.MuiSelect-select.MuiSelect-select {
  text-align: left !important; }
.MuiFormControl-root {
  margin: 0 !important;
  flex-grow: 1;
  max-width: 414px; }
.MuiSelect-select {
  &.MuiSelect-select {
    text-align: left !important;
    min-width: 100px !important; } }
.makeStyles-formControl-1 {
  flex-grow: 1; }
.MuiInput-underline {
  &:before {
    border-color: #ffc312 !important; }
  &:after {
    border-color: #65ddd9 !important; }
  &.Mui-error {
    &:after {
      border-color: #ff767b !important; } } }
.MuiMenuItem-root {
  display: flex !important;
  min-height: 36px !important; }
.MuiList-padding {
  padding: 0 !important; }
.inputForm .MuiInputBase-input {
  color: #ffc312 !important; }
.select__answer {
  width: 100%;
  &--right {
    color: green;
    // @include _768
 } }    //   color: yellow
.answer {
  &__tooltip {
    width: 100%;
    font-size: 16px;
    color: #ff767b; } }
.selectForm {
  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 5px;
    flex-wrap: wrap; } }
// ___ styles for radio task ___
.quiz {
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center; } }
.match {
  &__box {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: flex-start;
    width: 100%; } }
.radio {
  &__box {
    @extend .match__box;
    flex-direction: column; }
  .MuiTypography-h6 {
    color: #fff; }
  // .MuiTypography-root
  //   color: #ffc312 !important
  .MuiFormControlLabel-label {
    color: #ffc312; }
  .MuiFormControlLabel-root {
    color: #ffc312; }
  .MuiRadio-root {
    color: inherit; }
  .MuiRadio-colorSecondary.Mui-checked {
    color: #ffc312; } }
// ___ styles for match task ___
.task {
  &__heading {
    color: #65ddd9; }
  &__index {
    margin: 0 10px;
    min-width: 1.25rem;
    color: #fff; }
  &__phrase {
    text-align: left;
    color: #fff;
    margin: 0; } }
.match__picture-box {
  display: flex;
  align-items: center;
  max-width: 220px;
  margin: 5px;
  > div {
    position: relative; }
  > div > span {
    position: absolute;
    left: 10px;
    color: #ff767b; } }
